import CountDown from '@/components/min-countdown'
export default {
	components: {
		CountDown
	},
	data() {
		return {
			groups: [],
			count: 0,
			group_timeout: 0
		}
	},
	props: {
		value: {
			type: null
		},
		groupNumber: {
			type: null
		},
		product: {
			type: Object
		}
	},
	watch: {
		product: {
			handler(val) {
				this.init()
			},
			deep: true
		}
	},
	mounted() {
		this.init()
	},
	methods: {
		init() {
			if (this.$tools.has_addon('xshopgroupon') && this.product.groupon) this.getGrouponGroups()
		},
		getGrouponGroups() {
			let form = {
				product_id: this.value
			}
			this.$http.post('groupon.groups', form).then(res => {
				this.groups = res.data.list
				this.count = res.data.count
				this.group_timeout = res.data.group_timeout
			})
		},
		buy(item) {
			let data = {
				groupon_id: item.id,
				order_type: 1
			}
			this.$emit('add-group', data)
		}
	}
}
